\r\n                            删除\r\n                        </a-menu-item>\r\n                    </a-menu>\r\n                </a-dropdown>\r\n            </span>\r\n            <p slot=\"expandedRowRender\" slot-scope=\"record\" style=\"margin: 0\">\r\n                {{ record.description }}\r\n            </p>\r\n        </a-table>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nconst columns = [\r\n  { title: '姓名', dataIndex: 'name', key: 'name' },\r\n  {\r\n    title: '性别',\r\n    dataIndex: 'gender',\r\n    key: 'gender',\r\n    filters: [\r\n      { text: '男', value: 0 },\r\n      { text: '女', value: 1 },\r\n    ],\r\n  },\r\n  { title: '年龄', dataIndex: 'age', key: 'age' },\r\n  { title: '标签', dataIndex: 'tags', key: 'tags', scopedSlots: { customRender: 'tags' }},\r\n  { title: '地址', dataIndex: 'address', key: 'address' },\r\n  { title: '操作', dataIndex: '', key: 'x', scopedSlots: { customRender: 'action' } },\r\n];\r\n\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n        loading : false,\r\n        data : [],\r\n        columns,\r\n    };\r\n  },\r\n  methods : {\r\n    onSearch(value){\r\n        this.loading = true;\r\n        this.getData(value);\r\n    },\r\n    async getData(value){\r\n        const res = await this.$http.get(`/api/v1/getSearchResult?search_text=${value}`);\r\n        this.data = [...res.data];\r\n        this.loading = false;\r\n    },\r\n    onClick({ key }) {\r\n      console.log(key);\r\n    }\r\n  }\r\n};\r\n</script>\r\n<style scope>\r\n    .table-content{\r\n        min-height: 600px;\r\n        background: #fff;\r\n    }\r\n    .search-box{\r\n        margin-bottom: 20px;\r\n    }\r\n</style>"],"sourceRoot":""}]);

